/* eslint-disable react-hooks/exhaustive-deps */
/* eslint-disable camelcase */
/* eslint-disable prefer-promise-reject-errors */
import React, { useEffect, useState } from 'react';
import {
  Button,
  FormGroup,
  Input,
  Label,
  ModalBody,
  ModalHeader,
} from 'reactstrap';
import InputDisplay from '../../containers/application/InputDisplay';
import AppError from '../../helpers/AppError';
import instance from '../../helpers/axiosInstance';
import IntlMessages from '../../helpers/IntlMessages';
import { getCurrentUser } from '../../helpers/Utils';
import { Separator } from '../common/CustomBootstrap';
import StateButton from '../StateButton';

const AddCustomQuestionModal = ({ setOpenAddModal }) => {
  const [formValues, setFormValues] = useState({
    question: '',
    answer: 'Answer',
  });

  const [loading, setLoading] = useState(true);
  const [data, setData] = useState([]);
  const [error, setError] = useState('');

  const { access_token } = getCurrentUser();

  useEffect(() => {
    const fetchPrizes = async () => {
      try {
        const response = await instance({
          method: 'GET',
          url: `/getquestion`,
          headers: {
            Authorization: `Bearer ${access_token}`,
          },
        });

        if (response.data.success) {
          setData(response.data.Questations);
        } else {
          setError(response.data.message);
        }
        setLoading(false);
      } catch (err) {
        setError(err.message);
        setLoading(false);
      }
    };

    fetchPrizes();
  }, []);

  if (error) throw new AppError(error);

  const handleChange = (event, key, value = false) => {
    setFormValues({
      ...formValues,
      [key]: value ? event : event.target.value,
    });
  };

  const handleSubmit = async (newData) => {
    return new Promise((success, failed) => {
      const addQuestion = async () => {
        try {
          const response = await instance({
            method: 'POST',
            url: '/addquestion',
            data: JSON.stringify({ question: [{ ...newData }] }),
            headers: {
              Authorization: `Bearer ${access_token}`,
            },
          });

          if (response.data.success) {
            setFormValues({ ...formValues, ...newData });
            success('Question added successfully!');
          } else {
            failed('Failed to added question!');
          }
        } catch (err) {
          console.log(err);
          failed('Failed to added question!');
        }
      };

      addQuestion();
    });
  };

  return (
    <>
      <ModalHeader>
        <h2 className="lead">
          <IntlMessages id="challenges.modal.application-add-question-title" />
        </h2>
      </ModalHeader>

      <ModalBody>
        <FormGroup>
          <Label>
            <IntlMessages id="challenges.modal.question" />
          </Label>

          <Input
            type="text"
            value={formValues.question}
            onChange={(event) => handleChange(event, 'question')}
          />
        </FormGroup>

        <FormGroup className="row justify-content-end">
          <StateButton
            id="add-question-buttom"
            color="secondary"
            onClick={() => handleSubmit(formValues)}
            next={() => setOpenAddModal(false)}
            className="m-2"
          >
            <IntlMessages id="challenges.modal.application-add" />
          </StateButton>
          <Button
            color="danger"
            onClick={() => setOpenAddModal(false)}
            className="m-2"
          >
            <IntlMessages id="challenges.modal.application-cancel" />
          </Button>
        </FormGroup>

        <Separator className="mb-4" />

        {loading ? (
          <div className="d-flex justify-content-center m-4">
            <div className="loading" role="status">
              <span className="sr-only">Loading...</span>
            </div>
          </div>
        ) : (
          <>
            {data.map((item) => (
              <InputDisplay
                key={item.id}
                messageId={item.question}
                name="challenges-questions"
              />
            ))}
          </>
        )}
      </ModalBody>
    </>
  );
};

export default AddCustomQuestionModal;
